"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardHeader, CardTitle, CardContent, CardDescription } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Video, FileText, Target, Repeat, Calendar, CheckCircle, AlertTriangle, Clock, X, Loader2, Check, ChevronDown } from "lucide-react"
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible"

interface TopicPageProps {
  topicId: string
  studentId: string
}

interface Topic {
  id: string
  name: string
  description: string
  subject_id: string
}

interface StudyMaterial {
  id: string
  topic_id: string
  title: string
  content_type: "video" | "pdf" | "notes" | "slides" | "test" | "test-solution" | "reference" | "extra"
  resource_url: string
  created_at: string
}

interface TopicProgress {
  topic_id: string
  student_id: string
  video_xp: number
  test_xp: number
  revision_xp: number
  total_xp: number
  status: "MASTERED" | "GOOD" | "NEEDS_WORK" | "Not Started"
  progress_percentage: number
  test_score: number
  revision_1_completed: boolean
  revision_2_completed: boolean
  revision_3_completed: boolean
  revision_4_completed: boolean
}

interface Revision {
  topic_id: string
  student_id: string
  revision_number: number
  due_date: string
  is_completed: boolean
  is_overdue: boolean
}

interface TestScore {
  id: string
  student_id: string
  topic_id: string
  test_material_id: string
  score: number
  max_score: number
  created_at: string
}

interface GroupedMaterials {
  video: StudyMaterial[]
  notes: StudyMaterial[]
  pdf: StudyMaterial[]
  slides: StudyMaterial[]
  test: StudyMaterial[]
  "test-solution": StudyMaterial[]
  reference: StudyMaterial[]
  extra: StudyMaterial[]
}

interface TestAttempt {
  id: string
  student_id: string
  topic_id: string
  test_material_id: string
  attempt_number: number
  score: number
  max_score: number
  created_at: string
}

// Study Materials Component with Test Score Submission and Retest
function StudyMaterials({ materials, studentId, topicId }: { materials: StudyMaterial[]; studentId: string; topicId: string }) {
  const [testAttempts, setTestAttempts] = useState<Record<string, TestAttempt[]>>({})
  const [testInputs, setTestInputs] = useState<Record<string, number | undefined>>({})
  const [submittingTests, setSubmittingTests] = useState<Record<string, boolean>>({})
  const [retestingTests, setRetestingTests] = useState<Record<string, boolean>>({})
  const [loadingScores, setLoadingScores] = useState(true)
  const [expandedHistory, setExpandedHistory] = useState<Record<string, boolean>>({})
  const supabase = createClient()

  const groupMaterials = (items: StudyMaterial[]): GroupedMaterials => {
    const grouped: GroupedMaterials = {
      video: [],
      notes: [],
      pdf: [],
      slides: [],
      test: [],
      "test-solution": [],
      reference: [],
      extra: [],
    }

    items.forEach((item) => {
      if (item.content_type in grouped) {
        grouped[item.content_type as keyof GroupedMaterials].push(item)
      }
    })

    return grouped
  }

  // Fetch all test attempts
  useEffect(() => {
    const fetchTestAttempts = async () => {
      setLoadingScores(true)
      try {
        const testMaterials = materials.filter((m) => m.content_type === "test")
        
        const attempts: Record<string, TestAttempt[]> = {}
        
        for (const material of testMaterials) {
          const { data, error } = await supabase
            .from("tests")
            .select("*")
            .eq("student_id", studentId)
            .eq("test_material_id", material.id)
            .order("attempt_number", { ascending: false })

          if (!error && data) {
            attempts[material.id] = data
          } else {
            attempts[material.id] = []
          }
        }
        
        setTestAttempts(attempts)
      } catch (err) {
        console.error("Error fetching test attempts:", err)
      } finally {
        setLoadingScores(false)
      }
    }

    if (materials.length > 0) {
      fetchTestAttempts()
    }
  }, [materials, studentId, topicId])

  const handleTestScoreChange = (materialId: string, value: string) => {
    const numValue = value === "" ? undefined : Math.min(Math.max(parseInt(value) || 0, 0), 20)
    setTestInputs((prev) => ({
      ...prev,
      [materialId]: numValue,
    }))
  }

  const handleSubmitTestScore = async (materialId: string, isRetest: boolean = false) => {
    const score = testInputs[materialId]
    
    if (score === undefined || score === null) {
      return
    }

    const stateKey = isRetest ? `retest-${materialId}` : materialId
    setSubmittingTests((prev) => ({
      ...prev,
      [stateKey]: true,
    }))

    try {
      const currentAttempts = testAttempts[materialId] || []
      const nextAttemptNumber = currentAttempts.length > 0 
        ? Math.max(...currentAttempts.map(a => a.attempt_number)) + 1
        : 1

      const { data, error } = await supabase.from("tests").insert({
        student_id: studentId,
        topic_id: topicId,
        test_material_id: materialId,
        attempt_number: nextAttemptNumber,
        score,
        max_score: 20,
      }).select()

      if (error) throw new Error(`Failed to submit score: ${error.message}`)

      if (data && data.length > 0) {
        setTestAttempts((prev) => ({
          ...prev,
          [materialId]: [data[0], ...currentAttempts],
        }))
      }

      setTestInputs((prev) => ({
        ...prev,
        [materialId]: undefined,
      }))

      setRetestingTests((prev) => ({
        ...prev,
        [materialId]: false,
      }))
    } catch (err) {
      console.error("Error submitting test score:", err)
    } finally {
      setSubmittingTests((prev) => ({
        ...prev,
        [stateKey]: false,
      }))
    }
  }

  const grouped = groupMaterials(materials)
  const hasAnyMaterials = Object.values(grouped).some((arr) => arr.length > 0)

  if (!hasAnyMaterials) {
    return (
      <Card>
        <CardContent className="pt-6">
          <p className="text-center text-sm text-muted-foreground">No study materials available for this topic yet.</p>
        </CardContent>
      </Card>
    )
  }

  const getAttemptStatus = (score: number) => {
    return score >= 12 ? { label: "PASSED", color: "bg-green-100 text-green-800" } : { label: "FAILED", color: "bg-red-100 text-red-800" }
  }

  const renderSection = (
    title: string,
    icon: string,
    items: StudyMaterial[],
    buttonLabel: string,
    isTestSection: boolean = false
  ) => {
    if (items.length === 0) return null

    return (
      <div key={title}>
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">
              {icon} {title}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {items.map((item) => {
                const attempts = testAttempts[item.id] || []
                const latestAttempt = attempts.length > 0 ? attempts[0] : null
                const isShowingRetest = retestingTests[item.id]
                const currentInput = testInputs[item.id]
                const isSubmitting = submittingTests[isShowingRetest ? `retest-${item.id}` : item.id]

                return (
                  <div key={item.id} className="rounded-lg border p-4 hover:bg-muted/50">
                    <div className="flex items-start justify-between gap-4 mb-4">
                      <div className="flex-1">
                        <p className="font-medium text-sm">{item.title}</p>
                        <Badge variant="secondary" className="mt-2">
                          {item.content_type}
                        </Badge>
                      </div>
                      <Button
                        onClick={() => window.open(item.resource_url, "_blank")}
                        size="sm"
                        variant="outline"
                      >
                        {buttonLabel}
                      </Button>
                    </div>

                    {isTestSection && (
                      <div className="space-y-4 border-t pt-4">
                        {/* Latest Attempt Summary */}
                        {latestAttempt && !isShowingRetest && (
                          <div className="rounded-lg bg-muted p-4">
                            <div className="flex items-start justify-between">
                              <div>
                                <p className="text-sm font-semibold">
                                  Latest Score: {latestAttempt.score} / {latestAttempt.max_score}
                                </p>
                                <p className="text-xs text-muted-foreground mt-1">
                                  Attempt #{latestAttempt.attempt_number}
                                </p>
                                <p className="text-xs text-muted-foreground">
                                  Taken on: {new Date(latestAttempt.created_at).toLocaleDateString()}
                                </p>
                              </div>
                              <Badge className={getAttemptStatus(latestAttempt.score).color}>
                                {getAttemptStatus(latestAttempt.score).label}
                              </Badge>
                            </div>
                          </div>
                        )}

                        {/* New Attempt Form */}
                        {!latestAttempt || isShowingRetest ? (
                          <div className="flex items-end gap-2">
                            <div className="flex-1">
                              <label className="block text-xs font-medium text-muted-foreground mb-1">
                                Score (out of 20)
                              </label>
                              <Input
                                type="number"
                                min="0"
                                max="20"
                                placeholder="Enter your score"
                                value={currentInput ?? ""}
                                onChange={(e) => handleTestScoreChange(item.id, e.target.value)}
                                disabled={isSubmitting}
                              />
                            </div>
                            <Button
                              onClick={() => handleSubmitTestScore(item.id, isShowingRetest)}
                              disabled={currentInput === undefined || isSubmitting}
                              size="sm"
                            >
                              {isSubmitting ? (
                                <>
                                  <Loader2 className="h-3 w-3 mr-1 animate-spin" />
                                  Submitting
                                </>
                              ) : isShowingRetest ? (
                                "Resubmit"
                              ) : (
                                "Submit"
                              )}
                            </Button>
                            {isShowingRetest && (
                              <Button
                                onClick={() => {
                                  setRetestingTests((prev) => ({
                                    ...prev,
                                    [item.id]: false,
                                  }))
                                  setTestInputs((prev) => ({
                                    ...prev,
                                    [item.id]: undefined,
                                  }))
                                }}
                                variant="outline"
                                size="sm"
                              >
                                Cancel
                              </Button>
                            )}
                          </div>
                        ) : (
                          <Button
                            onClick={() => setRetestingTests((prev) => ({ ...prev, [item.id]: true }))}
                            variant="outline"
                            size="sm"
                            className="w-full"
                          >
                            <Repeat className="h-3 w-3 mr-2" />
                            Retest
                          </Button>
                        )}

                        {/* Attempt History */}
                        {attempts.length > 1 && (
                          <Collapsible
                            open={expandedHistory[item.id] || false}
                            onOpenChange={(open) =>
                              setExpandedHistory((prev) => ({
                                ...prev,
                                [item.id]: open,
                              }))
                            }
                          >
                            <CollapsibleTrigger asChild>
                              <Button variant="ghost" size="sm" className="w-full h-8">
                                <ChevronDown className="h-4 w-4 mr-2" />
                                Attempt History ({attempts.length})
                              </Button>
                            </CollapsibleTrigger>
                            <CollapsibleContent className="space-y-2 mt-2">
                              {attempts.map((attempt, idx) => (
                                <div
                                  key={attempt.id}
                                  className="flex items-center justify-between rounded-lg bg-muted p-3 text-sm"
                                >
                                  <div>
                                    <p className="font-medium">Attempt #{attempt.attempt_number}</p>
                                    <p className="text-xs text-muted-foreground">
                                      {new Date(attempt.created_at).toLocaleDateString()}
                                    </p>
                                  </div>
                                  <div className="flex items-center gap-2">
                                    <span className="font-semibold">
                                      {attempt.score}/{attempt.max_score}
                                    </span>
                                    <Badge className={getAttemptStatus(attempt.score).color}>
                                      {getAttemptStatus(attempt.score).label}
                                    </Badge>
                                  </div>
                                </div>
                              ))}
                            </CollapsibleContent>
                          </Collapsible>
                        )}
                      </div>
                    )}
                  </div>
                )
              })}
            </div>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {renderSection("Video Lectures", "üì∫", grouped.video, "Open Video")}
      {renderSection("Notes", "üìù", grouped.notes, "Open Notes")}
      {renderSection("PDFs", "üìÑ", grouped.pdf, "Open PDF")}
      {renderSection("Slides", "üìö", grouped.slides, "Open Slides")}
      {!loadingScores && renderSection("Test Papers", "üß™", grouped.test, "Open Test", true)}
      {renderSection("Test Solutions", "üß†", grouped["test-solution"], "Open Solution")}
      {renderSection("References", "üîó", grouped.reference, "Open Link")}
      {renderSection("Extra Resources", "üì¶", grouped.extra, "Open Resource")}
    </div>
  )
}

export function TopicPage({ topicId, studentId }: TopicPageProps) {
  const [topic, setTopic] = useState<Topic | null>(null)
  const [studyMaterials, setStudyMaterials] = useState<StudyMaterial[]>([])
  const [topicProgress, setTopicProgress] = useState<TopicProgress | null>(null)
  const [revisionSchedule, setRevisionSchedule] = useState<Revision[]>([])
  const [loading, setLoading] = useState(true)
  const [showTest, setShowTest] = useState(false)
  const [testScore, setTestScore] = useState<number>(0)
  const [error, setError] = useState<string | null>(null)

  const supabase = createClient()

  const fetchData = async () => {
    setLoading(true)
    setError(null)
    try {
      const [
        { data: topicData, error: topicError },
        { data: materialsData, error: materialsError },
        { data: progressData, error: progressError },
        { data: revisionsData, error: revisionsError },
      ] = await Promise.all([
        supabase.from("topics").select("*").eq("id", topicId).single(),
        supabase.from("study_materials").select("*").eq("topic_id", topicId).order("created_at", { ascending: true }),
        supabase.from("topic_progress").select("*").eq("topic_id", topicId).eq("student_id", studentId).single(),
        supabase.from("revision_schedule").select("*").eq("topic_id", topicId).eq("student_id", studentId).order("revision_number"),
      ])

      if (topicError) throw new Error(`Failed to fetch topic: ${topicError.message}`)
      if (materialsError) throw new Error(`Failed to fetch study materials: ${materialsError.message}`)
      if (progressError && progressError.code !== 'PGRST116') throw new Error(`Failed to fetch topic progress: ${progressError.message}`)
      if (revisionsError) throw new Error(`Failed to fetch revision schedule: ${revisionsError.message}`)

      setTopic(topicData)
      setStudyMaterials(materialsData || [])
      setTopicProgress(progressData)
      setRevisionSchedule(revisionsData || [])
      
      // Debug logging
      console.log("Topic:", topicData)
      console.log("Study Materials:", materialsData)
      console.log("Topic Progress:", progressData)
    } catch (err) {
      setError(err instanceof Error ? err.message : "An unknown error occurred")
      console.error("Error fetching data:", err)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchData()
  }, [topicId, studentId])

  const handleCompleteVideo = async () => {
    try {
      const { error } = await supabase.rpc("complete_topic_video", { p_student: studentId, p_topic: topicId })
      if (error) throw new Error(`Failed to complete video: ${error.message}`)
      await fetchData()
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to complete video")
      console.error("Error completing video:", err)
    }
  }

  const handleCompleteTest = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const { error } = await supabase.rpc("complete_topic_test", { p_student: studentId, p_topic: topicId, p_score: testScore })
      if (error) throw new Error(`Failed to complete test: ${error.message}`)
      setShowTest(false)
      setTestScore(0)
      await fetchData()
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to complete test")
      console.error("Error completing test:", err)
    }
  }

  const handleCompleteRevision = async (revNum: number) => {
    try {
      const { error } = await supabase.rpc("complete_topic_revision", { p_student: studentId, p_topic: topicId, p_rev: revNum })
      if (error) throw new Error(`Failed to complete revision ${revNum}: ${error.message}`)
      await fetchData()
    } catch (err) {
      setError(err instanceof Error ? err.message : `Failed to complete revision ${revNum}`)
      console.error(`Error completing revision ${revNum}:`, err)
    }
  }

  const dismissError = () => setError(null)

  if (loading) return <div className="flex justify-center items-center h-full"><Loader2 className="w-6 h-6 animate-spin" /></div>

  if (error) return (
    <div className="container mx-auto p-4">
      <Card className="border-destructive">
        <CardContent className="p-4">
          <div className="flex justify-between items-center">
            <div className="text-destructive">Error: {error}</div>
            <Button variant="ghost" size="sm" onClick={dismissError}>
              <X className="w-4 h-4" />
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )

  if (!topic) return <div className="text-center text-destructive">Topic not found.</div>

  const totalXp = (topicProgress?.video_xp || 0) + (topicProgress?.test_xp || 0) + (topicProgress?.revision_xp || 0)
  
  const getRevisionStatus = (revision: Revision) => {
    if (revision.is_completed) return { text: "Completed", icon: <CheckCircle className="w-4 h-4 text-green-500" />, color: "text-green-500" }
    if (revision.is_overdue) return { text: "Overdue", icon: <AlertTriangle className="w-4 h-4 text-red-500" />, color: "text-red-500" }
    return { text: "Pending", icon: <Clock className="w-4 h-4 text-gray-500" />, color: "text-gray-500" }
  }
  
  const getBadgeVariant = (status: string | null) => {
    switch (status) {
      case "MASTERED":
        return "default"
      case "GOOD":
        return "secondary"
      case "NEEDS_WORK":
        return "destructive"
      default:
        return "outline"
    }
  }

  return (
    <div className="container mx-auto p-4 md:p-6 space-y-6">
      {/* Section 1: Topic Header */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-start">
            <div>
              <CardTitle className="text-2xl font-bold">{topic.name}</CardTitle>
              <CardDescription className="mt-2">{topic.description}</CardDescription>
            </div>
            <div className="flex flex-col items-end space-y-2">
                <Badge variant={getBadgeVariant(topicProgress?.status ?? null)}>{topicProgress?.status || "Not Started"}</Badge>
                <span className="font-bold text-lnog">{totalXp} XP</span>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <Progress value={topicProgress?.progress_percentage || 0} className="w-full" />
           <p className="text-sm text-muted-foreground mt-2">{topicProgress?.progress_percentage || 0}% Complete</p>
        </CardContent>
      </Card>

      <div className="grid md:grid-cols-2 gap-6">
        <div className="space-y-6">
          {/* Section 2: Study Materials - Grouped by Content Type */}
          <StudyMaterials materials={studyMaterials} studentId={studentId} topicId={topicId} />

          {/* Section 3: Test Section */}
          <Card>
            <CardHeader>
              <CardTitle>Test Your Knowledge</CardTitle>
              {topicProgress?.test_score != null && <CardDescription>Previous Score: {topicProgress.test_score}%</CardDescription>}
            </CardHeader>
            <CardContent>
              {!showTest ? (
                <Button onClick={() => setShowTest(true)} className="w-full">
                  <Target className="w-4 h-4 mr-2" />
                  Start Test
                </Button>
              ) : (
                <form onSubmit={handleCompleteTest} className="space-y-4">
                  <Input
                    type="number"
                    placeholder="Enter your score (0-100)"
                    value={testScore}
                    onChange={(e) => setTestScore(parseInt(e.target.value))}
                    min="0"
                    max="100"
                    required
                  />
                  <div className="flex gap-2">
                    <Button type="submit" className="flex-1">Submit Score</Button>
                    <Button variant="outline" onClick={() => setShowTest(false)} className="flex-1">Cancel</Button>
                  </div>
                </form>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Section 4: Revision Section */}
        <Card>
          <CardHeader>
            <CardTitle>Training Dojo</CardTitle>
            <CardDescription>Revise this topic to achieve mastery.</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {revisionSchedule.length > 0 ? revisionSchedule.map((rev) => {
              const status = getRevisionStatus(rev)
              return (
                <Card key={rev.revision_number} className="p-4">
                  <div className="flex justify-between items-center">
                    <div>
                      <h4 className="font-semibold">Revision {rev.revision_number}</h4>
                      <div className="flex items-center gap-2 mt-1">
                        {status.icon}
                        <span className={`text-sm font-medium ${status.color}`}>{status.text}</span>
                      </div>
                      <div className="flex items-center gap-2 text-xs text-muted-foreground mt-1">
                        <Calendar className="w-3 h-3" />
                        Due: {new Date(rev.due_date).toLocaleDateString()}
                      </div>
                    </div>
                    {!rev.is_completed && <Button size="sm" onClick={() => handleCompleteRevision(rev.revision_number)}><Repeat className="w-4 h-4 mr-2" />Train Now</Button>}
                  </div>
                </Card>
              )
            }) : (
              <p className="text-muted-foreground text-center">No revisions scheduled yet.</p>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
